<template>
    <div class="skill container">
        <h2>专业技能</h2>
        <!-- <ul>
            <li v-for="(item,index) in ulList" :key="index" class="col-md-3 col-xs-6">
                <div class="title">{{item.title}}：</div>
                <div class="li-box">
                    <div class="li-bg" :style="{width:item.percentage + '%'}"></div>
                </div>
                <div class="li-num">{{item.percentage}}%</div>
            </li>
        </ul> -->
        <Row>
            <Col class="li" :xs="24" :md="8" v-for="(item,index) in ulList" :key="index">
                <div class="title">{{item.title}}：</div>
                <div class="li-box">
                    <div class="li-bg" :style="{width:item.percentage + '%'}"></div>
                </div>
                <div class="li-num">{{item.percentage}}%</div>
            </Col>
        </Row>
    </div>
</template>

<script>
    export default {
        name:'Skill',
        data() {
            return {
                ulList:[
                    {
                        title:'HTML5',
                        percentage:90
                    },
                    {
                        title:'CSS3',
                        percentage:90
                    },
                    {
                        title:'Javascript',
                        percentage:85
                    },
                    {
                        title:'jQuery',
                        percentage:90
                    },
                    {
                        title:'nodejs',
                        percentage:80
                    },
                    {
                        title:'MongoDB',
                        percentage:85
                    },
                    {
                        title:'Vue',
                        percentage:75
                    },
                    {
                        title:'git',
                        percentage:70
                    },
                    {
                        title:'webpack',
                        percentage:70
                    }
                ]
            }
        },
    }
</script>

<style lang="less" scoped>
@media screen and (min-width: 992px){
    .skill{
        padding-top: 0.8rem;
    }
}
.skill{
    margin-top: 10px;
    h2{
        font-size: 2em;
        text-align: center;
    }
    .li{
        font-size: 1.5em;
        display: flex;
        padding: 10px 0;
        .title{
            width: 120px;
        }
        .li-box{
            width: 150px;
            height: 10px;
            margin: 10px;
            background: #ccc;
            .li-bg{
                background: #2d8cf0;
                height: 100%;
            }
        }
    }
}
</style>


